.container{
   width: 100%;
}
.hidden{
    overflow: hidden;
    transition : transform 2s;
}


.slider__img{
    width: 100%;  
    height: auto;
}
.menu{
    margin-top:40px;
    text-align: center;
    font-size:30px;
    text-shadow:3px 3px 3px black
}
.menu_title{
    font-size:50px
}


.img__bar{
    width: 1250px;
    margin:0 auto;

}
.img__bar__box{    
    margin-top:30px;
    display: grid;
    grid-template-columns: repeat(3, 400px);
    justify-items: center;
} 
.img__bar__box img{
    width: 297px; height: 222px;
    margin-top:20px;
}
.golf_text{
    border:3px solid black;
    width: 420px;
    height: 500px;
    margin:0 auto;
    display:flex;
    align-items: center;
    justify-content: center;
}

.golf_text p{
    text-align: center;
}
.golf_text p span{
    display:block;
    margin-top:15px;
}
.price{
    color:red;
}
@media screen and (max-width:767px){
    .golf_text{
        width: 95%;
    }
    
    .slider{
        width: 100%;
        height: auto;
    }
    .img__bar{
        width: 500px;
    }
   .img__bar__box >img{
       width: 80vw;
       display:block;
       margin:0 auto;
        margin-bottom:20px;
   }
   .img__bar__box{
       display:block;
    
   }
}